export default {
	data() {
		return {
			show: null,
			uerInfo: null,
			delbutton: false,
		}
	},
	onLoad: function(e) {
		let data = JSON.parse(decodeURIComponent(e.data));
		this.show = data;
		this.uerInfo = getApp().globalData.userInfo;
		// console.log(this.uerInfo);
	},
	onShow: function() {
		console.log(this.uerInfo.nickName);
		console.log(this.show.user.name);
		if (this.uerInfo.nickName == this.show.user.name) {
			this.delbutton = true
		}
	},
	methods: {
		del(e) {
			console.log(e);
			let _this = this;
			uni.request({
				url: _this.$loginUrl + '/api/v1/del',
				data: {
					id: e.id,
				},
				method: 'GET',
				success: (res) => {
					console.log(res.data);
					if (res.data.code == 200) {
						uni.showToast({
							title: '删除成功',
							duration: 2000,
							icon: 'none',
						});
						uni.redirectTo({
							url: '/pages/forward/forward'
						});
					}
				},
				fail: (res) => {
					console.log(res.data);
				}
			});

		},
	}
}
